<template>
    <div class="page about-section white-bg p-70px-t">
        <div class="container-fluid p-0">
            <div class="row no-gutters">
                <div class="col-md-4 p-35px-t">
                    <div class="p-20px-l">
                        <div class="sub-title m-30px-b">
                            <h3 class="dark-color theme-after">Manage Patterns</h3>
                        </div>                        
                        <ol class="saved-patterns m-0px p-0px p-20px-l">
                            <li class="loading-list" v-if="isSavedPatternsloading">
                                <div class="spinner-grow text-primary" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-secondary" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-success" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-danger" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-warning" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-info" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-light" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                                <div class="spinner-grow text-dark" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </li>
                            <li class="p-5px-b" v-for="(pattern, key) in savedPatterns" :key="key">
                                &nbsp; <span v-for="(activeColor, alphabet) in pattern" v-bind:style="{ color: activeColor }" :key="generateKey(key, alphabet)">{{alphabet}}{{ alphabet.toLowerCase() }} </span> &nbsp; <a href="#" class="delete-button theme-color" v-on:click.prevent="deletePattern(key)">x</a>
                            </li>
                        </ol>
                        <app-manage-users></app-manage-users>
                    </div>
                </div>
                <div class="col-md-8 p-35px-t">
                    <div class="p-20px-lr">
                        <div class="sub-title m-30px-b">
                            <h3 class="dark-color theme-after">Manage Comments</h3>
                        </div>
                        <table class="table table-sm table-striped table-bordered m-0px p-0px p-20px-l">
                            <tr>
                                <th>#</th>
                                <th>Fullnames</th>
                                <th>Email</th>
                                <th>Comment</th>
                                <th>Status</th>
                                <th>Date created</th>
                                <th>Actions</th>
                            </tr>
                            <tr v-if="isLoadingComments">
                                <td colspan="7">
                                    <div class="spinner-grow text-warning"></div>
                                    <div class="spinner-grow text-danger"></div>
                                    <div class="spinner-grow text-success"></div>
                                    <div class="spinner-grow text-secondary"></div>
                                    <div class="spinner-grow text-primary"></div>
                                </td>
                            </tr>
                            <tr class="p-5px-b" v-for="(c, key, index) in comments" :key="key">
                                <td>{{ index + 1 }}</td>
                                <td>{{ c.fullnames }}</td>
                                <td>{{ c.email }}</td>
                                <td>{{ c.comment }}</td>
                                <td>{{ c.published ? '' : 'un' }}published</td>
                                <td>{{ c.timestamp }}</td>
                                <td> <a href="#" class="publish-button theme-color" v-on:click.prevent="publishComment(key, c)">=></a> |
                                    <a href="#" class="delete-button theme-color" v-on:click.prevent="deleteComment(key)">x</a>
                                </td>
                            </tr>
                        </table>

                        <div class="sub-title m-30px-tb">
                            <h3 class="dark-color theme-after">Edit Language templates/Content</h3>
                        </div>
                        <form method="post" v-on:submit.prevent="updateConfigs">
                            About Page Header
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <input v-model.trim="lang.en.aboutHeader" class="validate form-control" required="">
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <input v-model.trim="lang.fr.aboutHeader" class="validate form-control" required="">
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                            </div>
                            About Page Text
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <wysiwyg v-model.trim="lang.en.aboutText" />
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <wysiwyg v-model.trim="lang.fr.aboutText" />
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                            </div>
                            Objectives Page Header
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <input v-model.trim="lang.en.objectivesHeader" class="validate form-control" required="">
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <input v-model.trim="lang.fr.objectivesHeader" class="validate form-control" required="">
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                            </div>
                            Objectives Page Content
                            <div class="row">
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <wysiwyg v-model.trim="lang.en.objectivesPage" />
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <wysiwyg v-model.trim="lang.fr.objectivesPage" />
                                        <span class="input-focus-effect theme-bg"></span>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="send">
                                    <button class="btn btn-theme" type="submit">Update</button>
                                </div>
                                <span class="output_message"></span>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from "vue";
    import wysiwyg from "vue-wysiwyg";
    import "vue-wysiwyg/dist/vueWysiwyg.css";
    import manageUsers from "@/components/ManageUsers.vue"

    Vue.use(wysiwyg, { maxHeight: "250px" });
    const fb = require("@/firebase/Firebase");

    export default {
        components: {
            'app-manage-users': manageUsers
        },
        data: function () {
            return {
                loading: true,
                isSavedPatternsloading: true,
                isLoadingComments: true,
                key: null,
                lang: { 
                    en: {},
                    fr: {}
                },
                savedPatterns: {},
                comments: {}
            }
        },
        created: function () {
            let _this = this;
            fb.db.collection('languages').get().then((row) => {
                row.forEach((doc) => {
                    _this.key = doc.id;
                    _this.lang = doc.data().configs;
                });
            });

            fb.db.collection('patterns').get().then((row) => {
                row.forEach((doc) => {
                    _this.savedPatterns[doc.id] = doc.data().pattern;
                });
                _this.isSavedPatternsloading = false;
            });

            fb.db.collection('comments').get().then((row) => {
                row.forEach((doc) => {
                    _this.comments[doc.id] = doc.data().comment;
                });
                _this.isLoadingComments = false;
            });
        },    
        methods: {
            generateKey: function(key, alphabet) {
                return key + alphabet;
            },
            saveConfigs: function(evt) {
                let _this = this
                fb.db.collection('languages').add({ configs: this.lang })
                .then(function(docRef) {
                    _this.flashMessage.success({ message: 'Language Configs saved successfully!'});
                    console.log('Document written with ID: ', docRef.id)
                })
                .catch((error) => {
                    _this.flashMessage.error({ message: 'Error saving configs. Please try again'});
                    console.log("Error adding document: ", error);
                });
            },
            updateConfigs: function(evt) {
                let _this = this;
                fb.db.collection('languages').doc(this.key).update({ configs: this.lang })
                .then(function() {
                    _this.flashMessage.success({ message: 'Language Configs updated successfully!'});
                })
                .catch((error) => {
                    _this.flashMessage.error({ message: 'Error saving configs. Please try again'});
                    console.log("Error adding document: ", error);
                });
            },
            deletePattern: function(key) {
                let _this = this;
                if(key === 'fiRlYevZLQKqXiFZ4V1F')
                {
                    _this.flashMessage.error({ message: 'Default Pattern cannot be deleted!'});
                    return;
                }

                this.$delete(this.savedPatterns, key);

                fb.db.collection('patterns').doc(key).delete()
                .then(function(){
                    _this.flashMessage.success({ message: 'Pattern deleted!'});
                    console.log("deleted pattern: ", key);
                })
                .catch((error) => {
                    this.flashMessage.error({ message: 'Error, deleting pattern. Please try again'});
                    console.log("Error deleting document: ", error);
                });
            },
            publishComment: function(key, comment) {
                comment.published = 1;
                let _this = this;
                fb.db.collection('comments').doc(key).update({ "comment": comment })
                .then(function(docRef) {
                    _this.flashMessage.success({ message: 'Comment Published!'});
                    console.log('Config document updated with ID: ', docRef.id)
                })
                .catch((error) => {
                    _this.flashMessage.error({ message: 'Error, publishing comment. Please try again!'});
                    console.log("Error adding document: ", error);
                });
            },
            deleteComment: function(key) {
                let _this = this;
                this.$delete(this.comments, key);
                fb.db.collection('comments').doc(key).delete()
                .then(function() {
                    console.log("deleted comment: ", key);
                    _this.flashMessage.success({ message: 'Comment deleted!'});
                })
                .catch(function(error) {
                    _this.flashMessage.error({ message: 'Error, deleting comment!'});
                    console.log("Error deleting document: ", error);
                });
            }
        }
    }	
</script>