<template>
    <div class="m-30px-tb">
        <div class="sub-title">
            <h2 class="dark-color theme-after">Manage Users</h2>
        </div>
        <table class="table table-sm table-striped table-bordered m-0px m-30px-tb p-0px p-20px-l">
            <tr>
                <th>#</th>
                <th>Fullnames</th>
                <th>Email</th>
                <th>Status</th>
            </tr>
            <tr v-if="isLoadingUsers">
                <td colspan="6">
                    <div class="spinner-grow text-primary"></div>
                    <div class="spinner-grow text-secondary"></div>
                    <div class="spinner-grow text-success"></div>
                    <div class="spinner-grow text-danger"></div>
                    <div class="spinner-grow text-warning"></div>
                </td>
            </tr>
            <tr class="p-5px-b" v-for="(u, key, index) in users" :key="key">
                <td>{{ index + 1 }}</td>
                <td>{{ u.name }}</td>
                <td>{{ u.email }}</td>
                <td><a href="#" class="activate-button theme-color" v-on:click.prevent="activateUser(key, u)">{{ u.active ? '' : 'in' }}active</a></td>
            </tr>
        </table>
        <div class="sub-title m-30px-b">
            <h2 class="dark-color theme-after">Create User</h2>
        </div>
        <div id="sign-up" class="sign-up">
            <p v-if="errors.length">
                <b>Please correct the following error(s):</b>
                <ul>
                    <li v-for="error in errors">{{ error }}</li>
                </ul>
            </p>
            <form autocomplete="false">
                <div class="form-group">
                    <input class="form-control" type="email" v-model.trim="signupForm.email" placeholder="Email">
                </div>
                <div class="form-group">
                    <input class="form-control" type="text" v-model.trim="signupForm.fullnames" placeholder="Fullnames">
                </div>
                <div class="form-group">
                    <input class="form-control" type="password" v-model.trim="signupForm.password" placeholder="Password">
                </div>
                <div class="form-group">
                    <input class="form-control" type="password" v-model.trim="signupForm.passwordConfirm" placeholder="Password Confirm">
                </div>
            </form>
            <button class="btn btn-theme" v-on:click.prevent="signUp">Sign up</button>
        </div>
    </div>
</template>
<script>
    const fb = require("@/firebase/Firebase");
    export default {
        data: function() {
            return {
                isLoadingUsers: true,
                users: {},
                errors: [],
                signupForm: {
                    email: '',
                    fullnames: '',
                    password: '',
                    passwordConfirm: ''
                }
            }
        },
        created: function () {
            let _this = this;
            fb.db.collection('users').get().then(row => {
                row.forEach((doc) => {
                    _this.users[doc.id] = doc.data();
                });
                _this.isLoadingUsers = false;
            }).catch(err => {
                console.log(err)
            });
        },
        methods: {
            clearForm: function() {
                this.signupForm = {
                    email: '',
                    fullnames: '',
                    password: '',
                    passwordConfirm: ''
                };
            },
            activateUser: function(key, user) {
                user.active = !user.active;
                let _this = this;
                fb.db.collection('users').doc(key).update(user)
                .then(function() {
                    _this.flashMessage.success({ message: 'User '+ (! user.active ? 'Deactivated' : 'Activated') });
                    console.log('Config document updated with ID: '+ key);

                    _this.$delete(_this.users, key);
                    _this.users[key] = user;
                })
                .catch((error) => {
                    let status = ! user.active ? 'Deactivating' : 'Activating';
                    _this.flashMessage.error({ message: 'Error, ' + status +'  account. Please try again!'});
                    console.log("Error updating: ", error);
                });
            },
            signUp: function() {
                let _this = this;
                if ( this.checkForm() )
                {                
                    fb.auth.createUserWithEmailAndPassword(this.signupForm.email, this.signupForm.password).then(function(user) {
                        fb.db.collection('users').doc(user.user.uid).set({
                            email: _this.signupForm.email,
                            name: _this.signupForm.fullnames,
                            created_at: _this.createDate()
                        }).then((docRef) => {
                            console.log(docRef)
                        }).catch(err => {
                            console.log(err)
                        });
                        _this.clearForm();
                        _this.flashMessage.success({ message: 'New Account created successfully.'});
                    },
                    function(err) {
                        _this.flashMessage.error({ message: 'Error creating account,' + err.message });
                        alert("Error creating Account!"+ err.message);
                    }
                    );
                }
            },
            createDate: function () {
                let format = function(date) {
                    var hours = date.getHours();
                    var minutes = date.getMinutes();
                    var ampm = hours >= 12 ? 'pm' : 'am';
                    hours = hours % 12;
                    hours = hours ? hours : 12; // the hour '0' should be '12'
                    minutes = minutes < 10 ? '0'+minutes : minutes;
                    var strTime = hours + ':' + minutes + ' ' + ampm;
                    return date.getMonth()+1 + "/" + date.getDate() + "/" + date.getFullYear() + " " + strTime;
                }
                return format( new Date() );
            },
            checkForm: function () {
                this.errors = [];

                if( !this.signupForm.passwordConfirm || this.signupForm.passwordConfirm !== this.signupForm.password )
                    this.errors.push('Password Confirm does not match the password provided.');

                if (!this.signupForm.email || ! this.validEmail(this.signupForm.email))
                    this.errors.push('Valid email required.');

                if (!this.signupForm.fullnames)
                    this.errors.push('Fullnames is a required fields.');

                return (! this.errors.length) ? true : false;
            },
            validEmail: function (email) {
                var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                return re.test(email);
            }
        }
    }
</script>
